<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          :src="require('../assets/logo.png')"
          class="my-3"
          contain
          height="200"
        />
      </v-col>

      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          BI Ripper
        </h1>

        <p class="subheading font-weight-regular">
          Análise dos resultados de um processamento
        </p>
      </v-col>
    </v-row>
    <v-row class="text-center">
      <v-col cols="3">
        <v-select
          v-model="selectBrt"
          :items="brtList"
          label="BRT"
          outlined
          clearable
        ></v-select>
      </v-col>
      <v-col cols="3">
        <v-select
          v-model="selectDataProcessamento"
          :items="dataProcessamentoList"
          label="Data do processamento do ripper"
          outlined
          clearable
        ></v-select>
      </v-col>
      <v-col cols="3">
        <v-select
          v-model="selectDataReferencia"
          :items="dataReferenciaList"
          label="Data de referência processada"
          outlined
          clearable
        ></v-select>
      </v-col>
      <v-col cols="3">
        <v-btn
          block
          elevation="0"
          color="blue"
          x-large
          outlined
          @click="goToBIRemoto"
        >Buscar Dados</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'Home',
  title: 'Páginal Inicial',

  data: () => ({
    selectBrt: null,
    selectDataProcessamento: null,
    selectDataReferencia: null,
    brtList: [
      { value: 1, text: 'BRT BH' },
      { value: 2, text: 'BRT NEVES' }
    ],
    dataProcessamentoList: [
      '2021-02-27',
      '2021-03-03',
      '2021-03-04',
      '2021-03-08',
    ],
    dataReferenciaList: [
      '2020-12-04',
      '2020-12-05',
      '2020-12-06',
    ]
  }),

  methods: {
    goToBIRemoto: function () {
      if (!this.selectBrt || !this.selectDataProcessamento || !this.selectDataReferencia) {
        this.showToastLock('É necessário o preenchimento de todos os campos.', '', false)
        return
      }
      this.$router.push({ path: `/brt/${this.selectBrt}/processamento-ripper/${this.selectDataProcessamento}/${this.selectDataReferencia}` })
    },
    showToastLock: function(title, message, lock=true) {
      this.$swal({
        showConfirmButton: !lock,
        allowEscapeKey: !lock,
        allowEnterKey: !lock,
        allowOutsideClick: !lock,
        title: title,
        text: message,
        icon: 'info',
      });
    }
  }
}
</script>
