import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    path: '/brt/:idBRT(\\d+)/processamento-ripper/:dataProcessamento([0-9]{4}-[0-9]{2}-[0-9]{2})/:dataReferencia([0-9]{4}-[0-9]{2}-[0-9]{2})',
    name: 'BIRemoto',
    component: () => import(/* webpackChunkName: "BIRemoto" */ '../views/BIRemoto.vue')
  },
  {
    path: '/processamento-ripper/csv',
    name: 'BICsvLocal',
    component: () => import(/* webpackChunkName: "BICsvLocal.vue" */ '../views/BICsvLocal.vue')
  },
  {
    path: '/ajustes',
    name: 'Ajustes',
    component: () => import(/* webpackChunkName: "Ajustes" */ '../views/Ajustes.vue')
  },
  {
    path: '/version-info',
    name: 'VersionInfo',
    component: () => import(/* webpackChunkName: "VersionInfo" */ '../views/VersionInfo.vue')
  },
]

const router = new VueRouter({
  mode: 'hash', //  "hash" | "history" | "abstract".
  base: process.env.BASE_URL,
  routes
})

export default router
